.offers{
    width: 65%;
    height: 70vh;
    display: flex;
    margin: 100px;
    padding: 0px 140px;
    margin-bottom: 150px;
    background: linear-gradient(180deg, white 0%,pink 60%);
}
.offers-left{
    flex:1;
    flex-direction: column;
    display: flex;
    justify-content: center;
}
.offers-left h1{
    color: black;
    font-size: 80px;
    font-weight: 600;
}
.offers-left p{
    color:black;
    font-size: 22px;
    font-weight: 600;
}
.offers-left button{
    width: 282px;
    height: 70px;
    border-radius: 35px;
    background: red;
    border: none;
    color: white;
    font-size: 22px;
    font-weight: 500;
    margin-top: 30px;
    cursor: pointer;
}
.offers-right{
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    padding-top: 50px;
}